.comments{
    .write{
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        margin-top: 20px;
        img{
            width: 40px;
            height: 40px;
            border-radius: 50%;
            object-fit: cover;
        }
        input{
            flex: 1;
            padding: 7.5px 20px;
            align-self: center;
        }
        .sendlogo{
            cursor: pointer;
        }
    }
    .comment{
        margin: 30px 0px;
        display: flex;
        justify-content: space-between;
        .l{
            display: flex;
            gap: 15px;
            img{
                width: 40px;
                height: 40px;
                border-radius: 50%;
                object-fit: cover;
            }
            .info{
                display: flex;
                flex-direction: column;
                gap: 3px;
                align-items: flex-start !important;
                // border: 1px solid red;
                span{
                    font-weight: 500;
                    // border: 1px solid red;
                }
                p{
                    color: gray;
                    // border: 1px solid red;
                }
            }
        }
        .date{
            align-self: center;
            font-size: 12px;
            color: gray;
            font-weight: 100;
        }
    }
}